
body{
			padding: 0;
      margin: 0;
      color: #fff;
      overflow: hidden;
}
.comtent{
		    position: absolute;
		    top: 0;
		    left: 0;
		    width: 100%;
		    bottom: 0;
		    word-break: break-all;
		    box-sizing: border-box;
		    font-size: 16px;
		    -webkit-transition: all 5s;
		    transition: all 5s;
		    padding: 8px 8px 60px 8px;
		    letter-spacing: 1px;
		    line-height: 30px;
		    -webkit-user-select: none;
		       -moz-user-select: none;
		        -ms-user-select: none;
		            user-select: none;
		    text-indent: 20px;
        overflow: auto;
}
.comtent div {
        text-align: right;
        padding-right: 50px;
}
.comtent section{
        text-align: center;
        font-size: 20px;
        padding: 10px;
}
.flower{
        position: absolute;
		    top: 0;
		    left: 0;
		    width: 100%;
		    bottom: 0;
}
.enlp{
        width: 260px;
		    height: 240px;
		    position: absolute;
		    top: 50%;
		    left: 50%;
		    margin: -120px 0 0 -130px;
        text-align: center;
        color: #000;
        font-size: 30px;
}
.open{
		    width: 80px;
		    height: 80px;
		    position: absolute;
		    top: 50%;
		    left: 50%;
		    margin: -40px 0 0 -40px;
		    background: #000;
		    border-radius: 40px;
		    text-align: center;
		    line-height: 80px;
		    font-size: 30px;
        color: #fff;
}
.reset{
		    width: 50px;
		    height: 50px;
		    position: fixed;
		    right: 10px;
		    bottom: 10px;
		    background: #fff;
		    color: #000;
		    border-radius: 25px;
		    text-align: center;
		    line-height: 50px;
		    font-size: 20px;
}



